import React from 'react';
import {Link} from 'react-router-dom';
import './index.css'
function Nav(){
    return(
        <div className="menustyle">
        <nav >
            
            <ul>
                <Link to='Form'>
                <li>Form</li>
                </Link>

                <Link to='Forum'>
                <li>Forum</li>
                </Link>    

                <Link to='Contact'>
                <li>Contact</li>
                </Link>          
             </ul>
         </nav>
</div>
    );
}
export default Nav